import { useRef, useState } from 'react'
import './App.css'
import { Interface, JsonRpcProvider, ethers, formatEther } from 'ethers'
import erc721ABI from './abis/erc721.json'
import routerABI from './abis/router.json'
import forgeABI from './abis/forge.json'
import eruda from 'eruda'

eruda.init()

function App() {
  const [count, setCount] = useState(0)
  const [earnNums, setEarnNums] = useState(0)
  const [usdtAmount, setUsdtAmount] = useState(0)
  const [tokenAmount, setTokenAmount] = useState(0)
  const [pendingNums, setPendingNums] = useState(0)
  const [overNums, setOverNums] = useState(0)
  const [isBegin, setIsBegin] = useState(0)

  const inputRef = useRef<HTMLInputElement>(null)
  const CHAIN_RPC_URL = 'https://bsc-dataseed3.defibit.io'
  const ROUTER_ADDRESS = '0x10ED43C718714eb63d5aA57B78B54704E256024E'
  const NFT_ADDRESS = '0xdAb4C309944A61A42a1879FcC7381C47A5561377'
  const FORGE_ADDRESS = '0x5489e4d9490da17db72b8ac4e030df999d8b3167'
  const USDT_ADDRESS = '0x55d398326f99059ff775485246999027b3197955'
  const ONE_COIN_ADDRESS = '0x604448c8f009135e913c7e0e4bef692b0003a02a'


  const handleClick = async () => {
    if (inputRef.current) {
      const privateKey = inputRef.current.value
      const provider = new JsonRpcProvider(CHAIN_RPC_URL);
      const wallet = new ethers.Wallet(privateKey, provider)
      const router = new ethers.Contract(ROUTER_ADDRESS, routerABI, wallet)
      const forge = new ethers.Contract(FORGE_ADDRESS, forgeABI, wallet)
      const nft = new ethers.Contract(NFT_ADDRESS, erc721ABI, wallet)
      const forgeInterface = new Interface(forgeABI);
      setIsBegin(1)
      const cardsCount = await nft.balanceOf(wallet.address)
      setCount(Number(cardsCount))
      let nonce = await provider.getTransactionCount(wallet.address);
      const txDetails: {
        tx: string,
        hash: string,
        status: boolean
      }[] = [];

      for (let i = 0; i < Number(cardsCount); i++) {
        const cardId = await nft.tokenOfOwnerByIndex(wallet.address, i)
        const usdtAmount = await forge.earn(cardId)
        console.log(Number(cardId))
        console.log(Number(formatEther(usdtAmount)).toFixed(4))
        if (usdtAmount === BigInt(0)) {
          continue
        }

        setEarnNums(old => old + 1)
        setUsdtAmount(old => old + Number(formatEther(usdtAmount)))
        const amount = await router.getAmountsOut(usdtAmount, [USDT_ADDRESS, ONE_COIN_ADDRESS])
        const oneCoinAmount = amount[1] * BigInt(9) / BigInt(10)
        setTokenAmount(old => old + Number(formatEther(oneCoinAmount)))
        const data = forgeInterface.encodeFunctionData(
          'mintToken',
          [cardId, oneCoinAmount]
        );

        const tx = {
          from: wallet.address,
          to: FORGE_ADDRESS,
          data: data,
          nonce: nonce++,
          gasLimit: 3000000,
          gasPrice: BigInt(100000000),
          chainId: 56,
          value: 0
        };

        // 
        const signedTx = await wallet.signTransaction(tx);
        await provider.send('eth_sendRawTransaction', [signedTx]);
        setPendingNums(old => old + 1)
      }
      setIsBegin(2)
    }
  }

  return (
    <>
      <div>
        <input type="text" ref={inputRef} />
      </div>

      <div className="card">
        <button onClick={handleClick}>
          
        </button>
        <p>
          {isBegin === 1 ? `${count}` : isBegin === 0 ? '' : ''}
        </p>
        <p>
          {earnNums}
        </p>
        <p>
          {usdtAmount}
        </p>
        <p>
          {tokenAmount}
        </p>
        <p>
          {pendingNums}
        </p>
        <p>
          {overNums}
        </p>
      </div>

    </>
  )
}

export default App
